.contact{
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.277);
    
}
.contact h2{
    text-align: center;
    margin-bottom: 20px;
    font-size: 24px;

}
.contact-form{
    display: flex;
    flex-direction: column;
}
.form-group{
    margin-bottom: 15px;
}
.form-group label{
    display: block;
    margin-bottom: 5px;
    font-weight: bold;

}
.form-group input,
.form-group textarea{
    width: 98%;
    padding: 10px;
    border: 1px solid #ccc ;
    border-radius: 5px;
    font-size: 16px;
}

.form-group textarea{
    resize:vertical;
}
button{
    padding: 10px;
    border: none;
    background-color: #007bff;
    color: white;
    font-size: 16px;
    cursor: pointer;
    transition: background-color 0.3s;
}
button:hover{
    background-color: #0056b3;

}