.skills-section{
    max-width: 1200px;
    margin: 0 auto;
    padding: 4rem 2rem;
    text-align: center;
}

.section-subtitle{
    color: #8a9a5b;
    font-size: 0.9rem;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 1px;
    margin-bottom: 0.5rem;
}

.section-title{
    font-size: 2.5rem;
    font-weight: 700;
    margin-bottom: 1rem;
    color: #333;
}
.section-description{
    color: #666;
    max-width: 600px;
    margin: 0 auto 3rem;
}
.skills-container{
    display: flex;
    justify-content: center;
    gap: 2rem;
    flex-wrap: wrap;
}
.skill-card{
    background-color: #ffff;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0,0,0,0.211);
    padding: 2rem;
    width: 300px;

}
.skill-card h3{
    font-size: 1.5rem;
    margin-bottom: 1rem;
}

.progress-circle{
    position: relative;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    background: conic-gradient(#a0b75e calc(var(--percentage)*1%),#e0e0e0 0);
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto 1.5rem;
    transition: background 0.3s ease;
}

.progress-circle::before{
    content: " ";
    position: absolute;
    inset: 10px;
    border-radius: 50%;
    background: white;
}
.percentage{
    position: relative;
    font-size: 1.8rem;
    font-weight: bold;
    transition: opacity 0.3s ease-in-out;
    
}
.stat{
    display: flex;
    flex-direction: column;
}

.skill-stats{
    display: flex;
    justify-content: space-between;
    

}

.stat-value{
    font-size: 1.2rem;
    font-weight: bold;
    color: #8a9a5b;
    
}
.stat-label{
    font-size: 0.9rem;
    color: #666;
}
@media (max-width:768px) {
    .skill-container{
        flex-direction: column;
        align-items: center;
    }
    .skill-card{
        width: 100%;
        max-width: 300px;
    }
    
}